import React, {Component} from "react";
import {connect} from "react-redux";
import {fetchPostsTwo} from "../action";

class PostTwo extends Component {
  componentDidMount() {
    this.props.fetchPostsTwo();
  }

  renderList() {
    return this.props.postsTwo.map(post => (
      <ul className="last-news">
        <li className="item" key={post.id}>
          {post.title}
        </li>
      </ul>
    ));
  }

  render() {
    return <div>{this.renderList()}</div>;
  }
}

const mapStateToProps = state => ({
  postsTwo: state.postsTwo,
});

export default connect(
  mapStateToProps,
  {fetchPostsTwo}
)(PostTwo);
